/*
 *  Live-Popup-Creater By LancerComet at 23:36, 2016/7/24.
 *  # Carry Your World #
 *  ---
 *  Live Popup 创建类.
 *  Live Popup 将使用此类进行创建. 此类是 store 中 LivePopup 的数据的二次封装.
 */
import LivePopupObj, {keepAlivePopups} from './service.data.js'
import popups from './service.popups-store.js'

export default class LivePopupCreator {
  constructor (param) {
    this.param = param
    setTimeout(this.init.bind(this), 1)  // 推入任务队列初始化以保证 onConfirm 与 onCancel 执行.
  }

  beforeInit (func) {
    this.param.beforeInit = func
    return this  // 返回 this 至外部确保链式调用可用.
  }

  onInited (func) {
    // OnInited 将在弹窗组件 v-for 时以指令的方式执行.
    this.param.onInited = func
    return this
  }

  onConfirm (func) {
    this.param.onConfirm = func
    return this
  }

  onCancel (func) {
    this.param.onCancel = func
    return this
  }

  init () {
    var newPopup = new LivePopupObj(this.param)
    var popupID = newPopup.$id

    // 检测是否有 $id 相同弹窗.
    var samePopup = popups.filter(value => value.$id === popupID)
    if (samePopup.length) {
      console.caution(`检测到 $id 相同 livePopup "${this.param.$id}}", 请解决冲突.`)
      return
    }

    // 执行 beforeInit
    this.param.beforeInit && this.param.beforeInit()

    // 检查 keepAlivePopups 中是否有旧弹窗并创建.
    if (keepAlivePopups.length) {
      for (let i = 0, length = keepAlivePopups.length; i < length; i++) {
        let value = keepAlivePopups[i]
        if (!value) { continue }  // 不知为何使用 for 时取 length 可能会和数组不同步，因此如果取到空值需要停止执行.
        value.restore()  // 先还原参数.
        popups.push(value)  // 将旧的弹窗推送至数组最后,

        // 然后需要更新当前弹窗的 Index.
        const newIndex = popups.indexOf(value)
        popups[newIndex].$index = newIndex
      }
    }

    // 推入 store 并获取 index.
    const index = popups.push(newPopup) - 1

    // 获取 index 后将 index 更新至对象的 $index 上供之后使用.
    popups[index].$index = index
  }
}



