import Vue from 'vue'
import {randomString} from '@bilibili-live/utils'
import popups from './service.popups-store.js'

// 用于存放保存模式下的弹窗对象数据的数组.
export var keepAlivePopups = []

export default class PopupObj {
  constructor (param) {
    Object.keys(param).forEach(key => { this[key] = param[key] })

    this.$id = param.$id ? param.$id : `popup-${randomString()}`
    this.$disabled = false
    this.$out = false  // 退出动画控制标识.
    this.type = param.type || 'normal'
    this.lockWhenConfirm = param.lockWhenConfirm || false
    this.$keep = param.keep || false
  }

  unlock () {
    this.$disabled = false
  }

  lock () {
    this.$disabled = true
  }

  close () {
    return this.removePopup()
  }

  keep () {
    this.$keep = true
  }

  cancelKeep () {
    this.$keep = false
  }

  /*
   * 还原弹窗为初始状态.
   */
  restore () {
    this.$out = false
  }

  removePopup () {
    return new Promise((resolve, reject) => {
      this.$out = true
      setTimeout(() => {
        // 从 store 中删除弹窗对象.
        // popups 为 store.livePopups 的引用.
        popups.splice(this.$index, 1)

        // 如果是保存模式，将弹窗对象数据保存至数组.
        if (this.$keep) {
          // 检查 keepAlivePopups 是否拥有相同 $id 的弹窗, 如果有, 则复写.
          var sameOne = keepAlivePopups.filter(value => value.$id === this.$id)
          if (sameOne.length) {
            keepAlivePopups[keepAlivePopups.indexOf(sameOne[0])] = this
          } else {
            keepAlivePopups.push(this)
          }
        } else {
          // 如果修改为 $keep = false 则删除弹窗.
          delete keepAlivePopups[keepAlivePopups.indexOf(this)]
        }
        updateIndex(popups)  // 更新弹窗的 $index.

        // For Maxthon <= 4.4: 在 Vue.nextTick 中刷新样式强行刷新浏览器图形缓存.
        Vue.nextTick(function () {
          resolve()
          if (!navigator.userAgent.match(/Maxthon\/4.[0-4]/)) { return }
          window.document.body.style.backgroundColor = '#fefefe'
          setTimeout(() => { window.document.body.style.backgroundColor = '#fff' }, 1)
        })
      }, 200)
    })
  }
}

// Definition: 更新 $index 函数.
// 当弹窗有数量变动时将执行此函数.
function updateIndex (livePopups) {
  livePopups.forEach((value, index) => { livePopups[index].$index = index })
}



