import Vue from 'vue'
import VueJSONP from 'vue-jsonp'

Vue.use(VueJSONP, 10000)

export default {
  // 获取用户账号信息.
  getUserInfo () {
    return Vue.http.get(`/User/getUserInfo?ts=${Date.now()}`)
  },

  // 获取邮件提醒.
  getMsgCount (captcha) {
    return Vue.jsonp('//message.bilibili.com/api/notify/query.notify.count.do', {
      captcha,
      type: 'jsonp',
      ts: Date.now()
    })

  },

  // 获取主站 Captcha.
  getCaptcha () {
    return Vue.jsonp('//www.bilibili.com/plus/widget/ajaxGetCaptchaKey.php', {
      js: '',
      callbackQuery: 'cb',
      callbackName: 'f'
    })
  },

  // 进入我的直播间
  enterMyRoom () {
    return Vue.http.post('/liveact/myroom')
  }
}



