/**
 * BFS 图片地址生成函数.
 * 将生成带有目标尺寸后缀的图片地址.
 * 默认支持 gif, jpg, png 后缀.
 *
 * @param { String } imgLink - 图片地址.
 * @param { Number } width - 图片宽度, 默认 60.
 * @param { Number } height - 图片高度, 默认 60.
 * @param { RegExp } regExp - 后缀扩展名检测正则表达式.
 *  
 * @return { String }
 */
export default function (imgLink, width = 60, height = 60, regExp = /\.(gif|jpg|png)$/) {
    const expName = imgLink.match(regExp)
    return expName ? imgLink + (`_${width}x${height}` + expName[0]) : imgLink
}



