/*
 *  Random Emoji By LancerComet at 15:32, 2016/7/18.
 *  # Carry Your World #
 *  ---
 *  随机颜文字模块.
 *
 *  randomEmoji.happy(): 随机生成高兴表情.
 *  randomEmoji.sad(): 随机生成伤心表情.
 *  randomEmoji.shock(): 随机生成惊讶表情.
 *  randomEmoji.helpness(): 随机生成欠打表情.
 */
const emojiPool = {
  happy: ['(｀･ω･´)', '=‿=✧', '●ω●', '(/ ▽ \\)', '(=・ω・=)', '(●\'◡\'●)ﾉ♥', '<(▰˘◡˘▰)>', '(⁄ ⁄•⁄ω⁄•⁄ ⁄)', '(ง,,• ᴗ •,,)ง ✧'],
  shock: [',,Ծ‸Ծ,,', '(｀･д･´)', 'Σ( ° △ °|||)︴', '┌( ಠ_ಠ)┘', '(ﾟДﾟ≡ﾟдﾟ)!?'],
  sad: ['∑(っ °Д °;)っ', '＞︿＜', '＞△＜', '●︿●', '(´；ω；`)'],
  helpless: ['◐▽◑', 'ʅ（´◔౪◔）ʃ', '_(:3 」∠)_', '_(┐「ε:)_', '(/・ω・＼)', '(°▽°)ﾉ']
}

const exportObj = {}
for (var type in emojiPool) {
  ((type) => {
    exportObj[type] = () => getEmoji(type)
  })(type)
}

export default exportObj

function getEmoji (type) {
  return emojiPool[type][Math.floor(Math.random() * emojiPool[type].length)]
}



