var mouseLeaveTimeout = null

export default {
  showUserPanel () {
    clearTimeout(mouseLeaveTimeout)
  },

  hideUserPanel (callback) {
    mouseLeaveTimeout = setTimeout(() => callback && callback(), 250)
  }
}



