/**
 * Share module for browsers embedded in bilibili native apps
 *
 * see http://syncsvn.bilibili.co/app/wiki/blob/master/iOS/app_jsbridge.md
 *
 * @Author: Chris Hu
 */

import getScript from './getScript'

/**
 * Init Bili share config:
 * {
 *   type: 'web',
 *   title: '标题',
 *   text: '分享内容',
 *   url: 'http://xxx.xxx.xx/xxxxx',
 *   image_url: 'http://xxx.xx.xx/xx.jpg'
 * }
 *
 * NOTE: The biliapp has a white list of domains which you should consult the up floor guys
 *
 * see http://f2e.bilibili.co/demo/biliapp.html
 */
export function initShare (shareConfig) {
  // alert(navigator.userAgent)
  if (!(/BiliApp/i).test(navigator.userAgent)) { return }

  getScript('//static.hdslb.com/biliapp/biliapp.js', () => {
    const biliApp = BiliApp || window.biliapp // eslint-disable-line

    if (biliApp) {
      biliApp.ready(() => {
        // alert('setShareContent')
        // alert(JSON.stringify(shareConfig))

        biliApp.setShareContent(shareConfig)
      })
    } else {
      console.error('failed to load //static.hdslb.com/biliapp/biliapp.js')
    }
  })
}

/**
 * Update bili app share config
 */
export function updateShareConfig (shareConfig) {
  const biliApp = BiliApp || window.biliapp // eslint-disable-line

  if (biliApp) {
    biliApp.setShareContent(shareConfig)
  }
}




