/*
 *  Live-Cert-Popups By LancerComet at 10:37, 2016/7/21.
 *  # Carry Your World #
 *  ---
 *  直播认证弹框模块.
 */
import Vue from 'vue'
import LivePopup from '@bilibili-live/component.popup'
import { randomEmoji } from '@bilibili-live/utils'
require('./Live-Cert-Popups.styl')

Vue.use(LivePopup)

const vm = new Vue({})

export default {
  // Definition: 手机绑定提示.
  cellphoneBinding: function () {
    vm.livePopup({
      title: '您需要进行手机认证',
      html:
        '<div class="live-cert-popup cellphone-providing">' +
          '<div class="hint-img" role="image"></div>' +
          '<p class="t-center">您需要先绑定手机后才可以开启直播喔！' + randomEmoji.happy() + '</p>' +
        '</div>',
      width: 370,
      button: {
        confirm: '去绑定！',
        cancel: '取消'
      },
      onConfirm: function () {
        window.location.href = 'https://passport.bilibili.com/member/bindPhone?gourl=http://live.bilibili.com'
      }
    })
  },

  // Definition: 到期实名认证提示.
  roomExpired: function () {
    vm.livePopup({
      title: '您需要进行实名认证',
      html:
      '<div class="live-cert-popup room-expired">' +
      '<div class="hint-img" role="image"></div>' +
      '<p class="t-center">试用期已过，直播间被冻结，请完成实名认证后再次点击开播。</p>' +
      '<p class="t-center">当您完成实名认证之后就可以再度开播啦！' + randomEmoji.happy() + '</p>' +
      '</div>',
      width: 370,
      button: {
        confirm: '去认证！',
        cancel: '取消'
      },
      onConfirm: function () {
        window.location.href = 'https://account.bilibili.com/site/identification'
      }
    })
  },

  // Definition: 剩余时间提示.
  timingHint: function (timingText) {
    vm.livePopup({
      title: '您的试用到期时间……',
      html:
      '<div class="live-cert-popup timing-hint">' +
        '<div class="hint-img" role="image"></div>' +
        '<p class="t-center">您的直播间将在 ' + timingText + ' 时结束试用直播功能，还请留意 ' + randomEmoji.happy() + '</p>' +
        '<p class="t-center">当您完成实名认证之后就可以无限制直播啦！' + randomEmoji.happy() + '</p>' +
      '</div>',
      width: 500,
      button: {
        confirm: '去认证！',
        cancel: '先用着啦~'
      },
      onConfirm: function () {
        window.open('https://account.bilibili.com/site/identification')
        this.close()
      }
    })
  },

  // Definition: 直播间试用功能关闭.
  trailClosed: function () {
    vm.livePopup({
      title: '您需要进行实名认证',
      content: `直播试用功能已关闭，完成实名认证后就可以继续开播啦~ ${randomEmoji.happy()}`,
      width: 400,
      button: {
        confirm: '去认证！',
        cancel: '取消'
      },
      onConfirm: function () {
        window.open('https://account.bilibili.com/site/identification')
        this.close()
      }
    })
  },

  // Definition: 进入房间失败弹窗.
  failure: function (xhrObject) {
    vm.livePopup({
      title: '进入直播间失败',
      html:
      '<p>非常抱歉，进入直播间出现错误，还请您稍后再试 > <</p>' +
      '<p>出现此问题可能是您的网络情况不稳定，或是<del style="color: #ccc">土豆</del>服务器暂时宕机，可稍后尝试。如果频繁出现此提示，请等待一段时间或联系客服，在此对您造成的不便我们表示真诚的歉意... (´；ω；`)</p>' +
      '<p></p>' +
      '<p>错误原因：' + xhrObject.status + ' - ' + xhrObject.statusText + '</p>',
      width: 500,
      type: 'info',
      button: { confirm: '(╯°口°)╯(┴—┴' }
    })
  }
}



