import Vue from 'vue'
// 批量替换url
Vue.http.interceptors.push((request, next) => {
  request.credentials = true
  if (/^\/[^/]+/.test(request.url)) {
    request.url = '//api.live.bilibili.com' + request.url
  }
  next((response) => {
    return response
  })
})



