/*
 *  Top User Panel Data Define By LancerComet at 15:20, 2016/7/14.
 *  # Carry Your World #
 *  ---
 *  用户面板项配置定义.
 */

const config = {
  // 面板信息项配置.
  infoItems: [
    { label: '金瓜子', iconName: 'gold-seed', value: vm => vm.getCurrency.gold },  // 由于模块闭包问题，value 的数据时从组建的视图对象中获取，vm 代表指令执行表达式时传入的 this，详见 User-Panel.vue.
    { label: '银瓜子', iconName: 'silver-seed', value: vm => vm.getCurrency.silver },
    { label: '成就值', iconName: 'achieve', value: vm => vm.getAchieve }
  ],

  // 面板底部按钮项配置.
  // slot 内的指令将注册到 User-Panel 组件的视图对象上.
  ctrlBtns: [
    { label: '直播中心', iconName: 'live-center', href: '/i' },
    {
      label: '消息中心',
      iconName: 'msg-hinter',
      href: 'http://message.bilibili.com',
      slot: {
        name: 'user-panel-msg-count',
        template: '<span class="msg-count v-middle a-splashing" v-text="userData.msgCount" v-show="userData.msgCount" :class="{\'active\': userData.msgCount}"></span>'
      },
      onClick: vm => vm.msgCountBlurChecking()
    },
    { label: '退出登录', href: 'https://account.bilibili.com/login?act=exit', wide: true, colored: true, target: '_self', onClick: vm => vm.logout() }
  ]
}

// 设置 Slot 默认值防止 Vue 报错.
config.ctrlBtns.forEach((value, index, array) => {
  if (!value.slot) {
    config.ctrlBtns[index].slot = {}
  }
})

export default config



