/**
 *  Login Status by LancerComet at 18:21, 2016.07.13.
 *  # Carry Your World #
 *  ---
 *  登录状态判断函数.
 * 
 *  @returns {boolean|"First Login"}
 */
import Cookie from '../cookie'
export default () => (Cookie.hasItem('DedeUserID') && !Cookie.hasItem('LIVE_LOGIN_DATA')) ? 'First Login' : (Cookie.hasItem('DedeUserID') && Cookie.hasItem('LIVE_LOGIN_DATA'))



