import LivePopupCreator from './services/service.creator.js'
import Component from './component.vue'
import popups from './services/service.popups-store.js'

const settings = {
  componentName: 'live-popup'
}

export default {
  install (Vue, options) {
    Vue.component(settings.componentName, Component)

    /*
     *  Live Popup 弹窗组件数据创建函数.
     *  ---
     *  创建一个新的 Live Popup 并返回至外部，同时将弹窗数据保存至 store 中.
     *  LivePopup 构建方式可能违背 Vuex 的设计原则，Vuex 要求使用 dispatch 将数据存放至 store 中，
     *  而本模块直接操作 store.
     *  这么做一是希望能够在 Class 中直接获取到 store 中的数据，二是希望在 actions 中能够返回弹窗对象至外部达到链式调用的目的，三是利用 Vuex 来将数据自动分发到各个组件。
     *  因此如果要更改此部分逻辑，还请注意此处的不同之处。
     */
    Vue.prototype.livePopup = function (param) {
      return new LivePopupCreator(param, popups)
    }
  }
}



